/**
 * General Enwikuna License Manager Client admin styles. Dashboard etc.
 */
#wpcontent {
  position: relative;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-header > h1 {
  font-size: 38px;
  letter-spacing: 2px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-header #elmc-dashboard-about {
  color: #555d66;
  margin: 18px 0;
  font-size: 18px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content {
  margin-top: 25px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content #api-unavailable > h3 {
  font-size: 20px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content #api-unavailable > p {
  font-size: 16px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-name .host {
  font-size: 10px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-license input {
  margin-bottom: 3px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-note {
  max-width: 150px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-version .version {
  background: #eeeeee;
  padding: 3px 6px;
  border-radius: 3px;
  margin-right: 5px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-version .version.version-old {
  background: #eba3a3;
  color: #761919;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-version .version.version-latest {
  background: #c6e1c6;
  color: #5b841b;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-version .button.button-secondary {
  margin-top: 10px;
}
#wpcontent .wrap.elmc .elmc-page-content #elmc-dashboard-content .wp-list-table td.column-license .license-key {
  display: inline-block;
  border-radius: 3px;
  padding: 5px 10px;
  line-height: 23px;
  margin-bottom: 10px;
}
#wpcontent .wrap.elmc .elmc-page-content #log-viewer {
  background: #fff;
  border: 1px solid #e5e5e5;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.04);
  padding: 5px 20px;
}
#wpcontent .wrap.elmc .elmc-page-content #log-viewer > pre {
  font-family: monospace;
  white-space: pre-wrap;
  word-wrap: break-word;
}
#wpcontent .wrap.elmc .elmc-page-content #debugger-actions-wrapper {
  margin-bottom: 16px;
}
#wpcontent .wrap .plugins .notice .elmc-upgrade-notice {
  border-top: 1px solid #ffb900;
  margin: 0 -12px;
  padding: 0.5em 0 0.5em 12px;
}
#wpcontent .wrap .plugins .notice .elmc-upgrade-notice::before {
  display: none;
}
